html .popup {
  width: 100%;
  min-height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  overflow: hidden;
  position: fixed;
  top: 0;
  display: none;
}

html .popup_content {
  margin: 40px auto 0 auto;
  width: 60%;
  height: 400px;
  padding: 60px;
  background-color: #fff;
  opacity: 0.95;
  border-radius: 5px;
  box-shadow: 0 0 10px #000;
  position: relative;
}

html .popup_content-close {
  position: absolute;
  right: 32px;
  top: 32px;
  width: 32px;
  height: 32px;
  opacity: 0.3;
}

html .popup_content-close:hover {
  opacity: 1;
}

html .popup_content-close:before,
html .popup_content-close:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 33px;
  width: 2px;
  background-color: #333;
}

html .popup_content-close:before {
  transform: rotate(45deg);
}

html .popup_content-close:after {
  transform: rotate(-45deg);
}

