@media (max-width: 1095px) {
  html body .container nav .topMenu .menu {
    margin-left: 0;
  }

  html body .container .content .content_main {
    flex: 1 1 800px;
    max-width: inherit;
    justify-content: space-between;
  }

  html body .container .content .content_main .main {
    flex: 1 1 600px;
