  }

  html body .container .content .aside_left {
    flex: 1 1 700px;
    flex-direction: row;
    align-items: stretch;
    justify-content: space-around;
    background: #10447b;
    flex-wrap: wrap;
  }

  html body .container .content .aside_left_pos .aside_left_container {
    background-size: cover;
  }

  html body .container .content .aside_left_pos .aside_left_container .time .time_name {
    margin-left: 1px;
  }

  html body .container .content .aside_left_pos .aside_left_container .buy {
    width: 100%;
    right: 0;
  }

  html body .container .content .aside_left_pos .aside_left_container .buy_now {
    margin-left: 60px;
  }

  html body .container .content .aside_left .map {
    flex: 1 1 200px;
    max-width: 100%;
    min-height: 490px;
  }

  html body .container .content .aside_left #vk_widget {
    flex: 0 1 279px;
    min-height: 489px;
  }
}

@media (max-width: 1095px) and (max-width: 350px) {
  html body .container .content .aside_left {
    max-width: 100%;
  }
}

@media (max-width: 975px) {
